import { connect } from 'react-redux';
import ConfessionCopyContent from './ConfessionCopyContent';
import { notify } from 'src/actions/notification';
import { buildPost } from 'src/core/utils';
import copyClipboard from 'clipboard-copy';

function mapStateToProps(state) {
  return {};
}

function mapDispatchToProps(dispatch) {
  return {
    copy(confession) {
      const text = buildPost(confession);

      return copyClipboard(text).then(() => {
        dispatch(notify('Copied'));
      });
    },
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ConfessionCopyContent);
