
import { httpGet, httpPost, httpDelete } from '../http-client';

export const confessionApi = {
  list({ limit = 10, offset = 0, status } = {}) {
    return httpGet('/confessions', {
      qs: { limit, offset, status }
    });
  },

  update(confessionId, confession) {
    return httpPost(`/confessions/${confessionId}`, confession);
  },
};

export const authApi = {
  getCurrentUser() {
    return httpGet('/auth/user');
  },

  login({ username, password }) {
    return httpPost('/auth/login', { username, password });
  },
};

export const adminApi = {
  listUsers() {
    return httpGet('/admin/users');
  },

  getUser(userId) {
    return httpGet(`/admin/users/${userId}`);
  },

  blockUser(userId) {
    return httpPost(`/admin/users/${userId}/block`);
  },

  forceUserCloseChat(userId) {
    return httpPost(`/admin/users/${userId}/closechat`);
  },

  listMappings() {
    return httpGet('/admin/mappings');
  },

  addMapping({ waiter, target }) {
    return httpPost('/admin/mappings', { waiter, target });
  },

  removeMapping(target) {
    return httpDelete(`/admin/mappings/${target}`);
  },
};

export const modApi = {
  unblockUser(code) {
    return httpPost(`/mod/users/${code}/unblock`);
  },

  getUserProfile(code) {
    return httpGet(`/mod/users/${code}`);
  },
};

export const activeUserApi = {
  list({ limit = 10, offset = 0, gender, lastActive, age } = {}) {
    return httpGet('/active-users/list', {
      qs: { limit, offset, gender, lastActive, age }
    });
  },
};
