import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { PAGE_SIZE } from 'src/constants';
import ConfessionListItem from '../ConfessionListItem';
import ConfessionListFilter from '../ConfessionListFilter';
import GotoPage from '../GotoPage';

const styles = theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  header: {
    width: '600px',
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
  },
  pagination: {
    width: '600px',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  flexRow: {
    display: 'flex',
  },
  nextButton: {
    marginLeft: theme.spacing(1),
  },
});

class ConfessionList extends React.Component {

  constructor(props) {
    super(props);

    this._onBackButtonClicked = this._onBackButtonClicked.bind(this);
    this._onNextButtonClicked = this._onNextButtonClicked.bind(this);
  }

  componentDidMount() {
    this.props.refresh();
  }

  render() {
    const { classes, confessions, isFetching, total, page, refresh } = this.props;
    const totalPage = Math.ceil(total / PAGE_SIZE);
    const disableBack = page < 1;
    const disableNext = page >= totalPage - 1;

    return (
      <div className={classes.container}>
        <div className={classes.header}>
          <ConfessionListFilter />
          <Button onClick={refresh}>Ti li</Button>
        </div>
        {
          totalPage > 0 &&
          <div className={classes.pagination}>
            <Button variant="outlined" color="primary" disabled={disableBack} onClick={this._onBackButtonClicked}>Back</Button>
            <Typography variant="body1">{`Trang ${page + 1}/${totalPage} (${total} confessions)`}</Typography>
            <div className={classes.flexRow}>
              <GotoPage />
              <Button className={classes.nextButton} variant="outlined" color="primary" disabled={disableNext} onClick={this._onNextButtonClicked}>Next</Button>
            </div>

          </div>
        }
        {isFetching ? <CircularProgress /> : confessions.map(cfs =>
          <ConfessionListItem key={cfs.id} confession={cfs} />
        )}
        <br />
        {!isFetching && <Button onClick={refresh}>Ti li</Button>}
        <br />
      </div>
    );
  }

  _onBackButtonClicked() {
    this.props.loadPage(this.props.page - 1);
  }

  _onNextButtonClicked() {
    this.props.loadPage(this.props.page + 1);
  }
}

export default withStyles(styles)(ConfessionList);
