import {
  FETCH_REQUEST,
  FETCH_SUCCESS,
  SET_STATUS_FILTER,
  TOGGLE_STATUS,
  SET_NUMBER,
} from 'src/actions/confessions';
import { Status } from 'src/constants';

const initialState = {
  items: [],
  total: 0,
  page: 0,
  isFetching: false,
  statusFilter: Status.UNCHECKED,
};

export default function(state = initialState, action) {
  switch (action.type) {
    case FETCH_REQUEST:
      return Object.assign({}, state, {
        isFetching: true,
      });

    case FETCH_SUCCESS:
      return Object.assign({}, state, {
        items: action.items,
        total: action.total,
        page: action.page,
        isFetching: false,
      });

    case SET_STATUS_FILTER:
      return Object.assign({}, state, {
        statusFilter: action.status,
      });

    case TOGGLE_STATUS:
      return Object.assign({}, state, {
        items: state.items.map((item) => {
          if (item.id === action.id) {
            return Object.assign({}, item, {
              status: action.status === item.status ? 0 : action.status,
            });
          }

          return item;
        }),
      });

    case SET_NUMBER:
      return Object.assign({}, state, {
        items: state.items.map((item) => {
          if (item.id === action.id) {
            return Object.assign({}, item, {
              number: action.number,
            });
          }

          return item;
        }),
      });

    default:
      return state;
  }
}
