import { connect } from 'react-redux';
import UnblockUserButton from './UnblockUserButton';
import { notify } from 'src/actions/notification';

function mapStateToProps(state) {
  return {};
}

function mapDispatchToProps(dispatch) {
  return {
    onUnblockSuccess() {
      dispatch(notify('M kha thnh cng'));
    },

    onUnblockFailure() {
      dispatch(notify('C li xy ra!'));
    }
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(UnblockUserButton);
