import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import Typography from '@material-ui/core/Typography';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import { calculateAge } from 'src/core/utils';
import { ListItemSecondaryAction, IconButton } from '@material-ui/core';
import RemoveIcon from '@material-ui/icons/Delete';

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  inline: {
    display: 'inline',
  },
  avatar: {
    marginRight: 10,
    width: 64,
    height: 64,
  },
}));

export default function MappingUserListItem({ user, onRemove }) {
  const classes = useStyles();
  const avatarUrl = `https://graph.facebook.com/${user.accounts[0].id}/picture`;
  const ageRange = (user.accounts[0].data && user.accounts[0].data.age_range) || { min: 0, max: 0 };
  const actualAge = calculateAge(user.birthday && new Date(user.birthday));

  const [isBigAvatar, setIsBigAvatar] = React.useState();

  return (
    <ListItem alignItems="flex-start" divider>
      { isBigAvatar && <img src={avatarUrl + '?height=640'} alt={`${user.name}'s avatar`} onClick={() => setIsBigAvatar(false)} /> }
      <ListItemAvatar onClick={() => setIsBigAvatar(true)}>
        <Avatar alt={`${user.name}'s avatar`} src={avatarUrl + '?height=100'} className={classes.avatar} />
      </ListItemAvatar>
      <ListItemText
        primary={user.name}
        secondary={
          <React.Fragment>
            <Typography
              component="span"
              variant="body2"
              className={classes.inline}
              color="textPrimary"
            >
              {user.id}
            </Typography>
            <span>{'  '}
              min: {ageRange.min} - max: {ageRange.max} - actual: {actualAge}
            </span>
          </React.Fragment>
        }
      />
      <ListItemSecondaryAction>
        <IconButton onClick={() => onRemove(user)}><RemoveIcon /></IconButton>
      </ListItemSecondaryAction>
    </ListItem>
  );
}
