import { SERVER_URL } from '../config';
import { getToken } from './auth';

const JSON_HEADER = {
  'Content-Type': 'application/json'
};

export function httpGet(path, options = {}) {
  return httpClient(path, Object.assign({
    headers: withAuthHeader(JSON_HEADER),
    mode: 'cors',
  }, options));
}

export function httpPost(path, body, options = {}) {
  return httpClient(path, Object.assign({
    method: 'POST',
    body: JSON.stringify(body),
    headers: withAuthHeader(JSON_HEADER),
    mode: 'cors',
  }, options));
}

export function httpDelete(path, options = {}) {
  return httpClient(path, Object.assign({
    method: 'DELETE',
    headers: withAuthHeader(JSON_HEADER),
    mode: 'cors',
  }, options));
}

function httpClient(path, options) {
  const url = new URL(`${SERVER_URL}${path}`);

  if (options.qs) {
    Object.keys(options.qs).forEach((key) => {
      const value = options.qs[key];

      if (value !== undefined) {
        url.searchParams.append(key, value);
      }
    });
  }

  return fetch(url, options)
    .then(responseToJson)
    .then(validateResponse);
}

function responseToJson(resp) {
  if (resp.ok) {
    return resp.json();
  }

  return Promise.reject(resp);
}

function validateResponse(resp) {
  if (resp.error_code) {
    return Promise.reject(resp);
  }

  return resp;
}

function withAuthHeader(headers) {
  const token = getToken();

  if (token) {
    return Object.assign({
      authorization: 'bearer ' + token,
    }, headers);
  }

  return headers;
}
