import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import Typography from '@material-ui/core/Typography';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import { calculateAge } from 'src/core/utils';

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  inline: {
    display: 'inline',
  },
  avatar: {
    marginRight: 10,
    width: 64,
    height: 64,
  },
}));

export default function UserListItem({ user }) {
  const classes = useStyles();
  const account = user.accounts ? user.accounts[0] : {};
  const avatarUrl = `https://graph.facebook.com/${account.id}/picture`;
  const ageRange = (account.data && account.data.age_range) || { min: 0, max: 0 };

  const [isBigAvatar, setIsBigAvatar] = React.useState();
  const actualAge = React.useMemo(() => {
    if (user.actualAge) {
      return user.actualAge;
    }

    return calculateAge(user.birthday && new Date(user.birthday)) || 99;
  }, [user]);

  return (
    <ListItem alignItems="flex-start" divider>
      { isBigAvatar && <img src={avatarUrl + '?height=640'} alt={`${user.name}'s avatar`} onClick={() => setIsBigAvatar(false)} /> }
      <ListItemAvatar onClick={() => setIsBigAvatar(true)}>
        <Avatar alt={`${user.name}'s avatar`} src={avatarUrl + '?height=100'} className={classes.avatar} />
      </ListItemAvatar>
      <ListItemText
        primary={user.name}
        secondary={
          <React.Fragment>
            <Typography
              component="span"
              variant="body2"
              className={classes.inline}
              color="textPrimary"
            >
              {user.id}
            </Typography>
            <span>{'  '}
              min: {ageRange.min} - max: {ageRange.max} - actual: {actualAge}
            </span>
            {user.lastActive && <span>{'  '}
              Active: {new Date(user.lastActive).toLocaleString()}
            </span>}
          </React.Fragment>
        }
      />

    </ListItem>
  );
}
