import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

function GotoPage({ loadPage }) {
  const [page, setPage] = React.useState(1);
  const [open, setOpen] = React.useState(false);

  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);
  const onChange = (event) => setPage(parseInt(event.target.value));
  const handleGo = () => {
    handleClose();
    loadPage(page - 1);
  };

  return (
    <div>
      <Button variant="outlined" color="primary" onClick={handleOpen}>
        Go
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Chuyn ti trang</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Nhp trang mun chuyn ti:
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="page"
            label="Trang"
            type="number"
            fullWidth
            value={page}
            onChange={onChange}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>
            Cancel
          </Button>
          <Button onClick={handleGo} color="primary">
          Go
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default React.memo(GotoPage);
