import { connect } from 'react-redux';
import * as auth from 'src/core/auth';
import PrivateRoute from './PrivateRoute';

function mapStateToProps(state, ownProps) {
  const user = state.auth.user;
  const { authzCheck = true } = ownProps;
  let isAllowed = true;

  if (authzCheck === true) {
    isAllowed = user && auth.isAllowed(user);
  } else if (Array.isArray(authzCheck)) {
    isAllowed = user && authzCheck.some(role => auth.isRole(role, user.role));
  }

  return {
    isAuthenticated: !!user,
    isAllowed,
  };
}

function mapDispatchToProps(dispatch) {
  return {};
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PrivateRoute);
