import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import ConfessionListFilter from './ConfessionListFilter';
import { loadPage, applyStatusFilter } from 'src/actions/confessions';

function mapStateToProps(state) {
  const { items, statusFilter } = state.confessions;

  return {
    confessions: items,
    statusFilter,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({
    loadPage,
    applyStatusFilter,
  }, dispatch);
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ConfessionListFilter);
