import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import { Status } from 'src/constants';

const styles = theme => ({
  formControl: {
    margin: theme.spacing(3),
  },
  group: {
    margin: `${theme.spacing(1)}px 0`,
    display: 'flex',
    flexDirection: 'row',
  },
});

class ConfessionListFilter extends React.Component {

  handleChange = event => {
    this.props.applyStatusFilter(parseInt(event.target.value));
  };

  render() {
    const { classes, statusFilter } = this.props;

    return (
      <FormControl component="fieldset" className={classes.formControl}>
        <RadioGroup
          aria-label="Lc"
          name="filter"
          className={classes.group}
          value={String(statusFilter)}
          onChange={this.handleChange}
        >
          <FormControlLabel value="-1" control={<Radio />} label="Tt c" />
          <FormControlLabel value={String(Status.REJECTED)} control={<Radio />} label="T chi" />
          <FormControlLabel value={String(Status.APPROVED)} control={<Radio />} label=" duyt" />
          <FormControlLabel value={String(Status.UNCHECKED)} control={<Radio />} label="Cha duyt" />
        </RadioGroup>
      </FormControl>
    );
  }
}

export default withStyles(styles)(ConfessionListFilter);
