import {
  SHOW,
  HIDE,
  SET,
} from 'src/actions/notification';

const initialState = {
  opened: false,
  messageText: '',
  messageKey: '',
};

export default function(state = initialState, action) {
  switch (action.type) {
    case SHOW:
      return Object.assign({}, state, {
        opened: true,
      });

    case HIDE:
      return Object.assign({}, state, {
        opened: false,
      });

    case SET:
      return Object.assign({}, state, {
        messageKey: action.messageKey,
        messageText: action.messageText,
      });

    default:
      return state;
  }
}
