import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import RefreshIcon from '@material-ui/icons/Refresh';
import Container from './Container';
import UserList from './UserList';
import useList from 'src/hooks/useList';
import { activeUserApi } from 'src/core/api';

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  toolbar: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    width: 900,
    '& label': {
      marginLeft: 5,
      marginRight: 5,
    },
    '& select': {
      marginLeft: 5,
    }
  },
}));

function listFn({ limit, offset, filters }) {
  const listOptions = { limit, offset, ...filters };

  if (listOptions.active) {
    listOptions.lastActive = Date.now() - 3600000 * listOptions.active;
    delete listOptions.active;
  }

  if (listOptions.gender === 'null') {
    delete listOptions.gender;
  }

  if (!listOptions.age) {
    delete listOptions.age;
  }

  return activeUserApi.list(listOptions).then((resp) => {
    return {
      items: resp.data.items,
      total: resp.data.total,
    };
  });
}

export default function ActiveUsersView() {
  const classes = useStyles();
  const list = useList({ listFn, defaultPageSize: 50, defaultFilters: { age: 20, gender: 'female', active: 3 } });

  React.useEffect(list.refresh, []);

  return (
    <Container className={classes.container}>
      <div className={classes.toolbar}>
        <div>
          <label>
            Min age:
            <select value={list.filters.age} onChange={event => list.addFilter({ age: parseInt(event.target.value) })}>
              <option value="null">-- Age --</option>
              <option value="18">18</option>
              <option value="19">19</option>
              <option value="20">20</option>
              <option value="21">21</option>
              <option value="22">22</option>
              <option value="23">23</option>
              <option value="24">24</option>
              <option value="25">25</option>
              <option value="26">26</option>
              <option value="27">27</option>
              <option value="28">28</option>
              <option value="29">29</option>
            </select>
          </label>
          <label>
            Active:
            <select value={list.filters.active} onChange={event => list.addFilter({ active: parseInt(event.target.value) })}>
              <option value="1">last 1 hour</option>
              <option value="3">last 3 hours</option>
              <option value="6">last 6 hours</option>
              <option value="12">last 12 hours</option>
              <option value="24">last 1 day</option>
              <option value="48">last 2 days</option>
              <option value="72">last 3 days</option>
              <option value="96">last 4 days</option>
              <option value="120">last 5 days</option>
              <option value="144">last 6 days</option>
              <option value="168">last 7 days</option>
              <option value="192">last 8 days</option>
              <option value="216">last 9 days</option>
            </select>
          </label>
          <label>
            Gender:
            <select value={list.filters.gender} onChange={event => list.addFilter({ gender: event.target.value })}>
              <option value="null">-- Gender --</option>
              <option value="female">female</option>
              <option value="male">male</option>
              <option value="other">other</option>
            </select>
          </label>
        </div>
        <div>
          <span>{list.total} ({list.page + 1} / {list.totalPage})</span>
          <input style={{ marginLeft: 10, width: 50 }} onChange={(event) => event.target.value && list.changePage(parseInt(event.target.value) - 1)} />
          <select value={list.pageSize} onChange={event => list.changePageSize(parseInt(event.target.value))}>
            <option value="10">10</option>
            <option value="20">20</option>
            <option value="50">50</option>
            <option value="100">100</option>
            <option value="200">200</option>
          </select>
        </div>
        <div>
          <IconButton onClick={list.refresh}><RefreshIcon /></IconButton>
          <ButtonGroup size="small">
            <Button disabled={list.page === 0} onClick={() => list.changePage(list.page - 1)}>Back</Button>
            <Button disabled={list.page === list.totalPage - 1} onClick={() => list.changePage(list.page + 1)}>Next</Button>
          </ButtonGroup>

        </div>
      </div>
      <UserList users={list.items} isFetching={list.isFetching} />
    </Container>
  );
}
