import {
  SHOW_SIDEBAR,
  HIDE_SIDEBAR,
} from 'src/actions/ui';

const initialState = {
  sidebarOpened: true,
};

export default function(state = initialState, action) {
  switch (action.type) {
    case SHOW_SIDEBAR:
      return Object.assign({}, state, {
        sidebarOpened: true,
      });

    case HIDE_SIDEBAR:
      return Object.assign({}, state, {
        sidebarOpened: false,
      });

    default:
      return state;
  }
}
