import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import HomeIcon from '@material-ui/icons/Home';
import UsersIcon from '@material-ui/icons/People';
import ModerateIcon from '@material-ui/icons/VerifiedUser';
import SidebarMenuItem from '../SidebarMenuItem';

const drawerWidth = 240;

const styles = theme => ({
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
});

function Sidebar({ classes, open, isAdmin, closeSidebar }) {
  return (
    <Drawer
      className={classes.drawer}
      variant="persistent"
      anchor="left"
      open={open}
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <div className={classes.drawerHeader}>
        <IconButton onClick={closeSidebar}>
          <ChevronLeftIcon />
        </IconButton>
      </div>
      <Divider />
      <List>
        <SidebarMenuItem to="/">
          <ListItemIcon>{<HomeIcon />}</ListItemIcon>
          <ListItemText primary="Home" />
        </SidebarMenuItem>

        <SidebarMenuItem to="/moderate">
          <ListItemIcon>{<ModerateIcon />}</ListItemIcon>
          <ListItemText primary="Moderate" />
        </SidebarMenuItem>

        {isAdmin && <SidebarMenuItem to="/online-users">
          <ListItemIcon>{<UsersIcon />}</ListItemIcon>
          <ListItemText primary="Online Users" />
        </SidebarMenuItem>}

        {isAdmin && <SidebarMenuItem to="/active-users">
          <ListItemIcon>{<UsersIcon />}</ListItemIcon>
          <ListItemText primary="Active Users" />
        </SidebarMenuItem>}

        {isAdmin && <SidebarMenuItem to="/mappings">
          <ListItemIcon>{<UsersIcon />}</ListItemIcon>
          <ListItemText primary="Mappings" />
        </SidebarMenuItem>}

        {isAdmin && <SidebarMenuItem to="/block-user">
          <ListItemIcon>{<UsersIcon />}</ListItemIcon>
          <ListItemText primary="Block User" />
        </SidebarMenuItem>}
      </List>
    </Drawer>
  );
}

export default withStyles(styles)(Sidebar);
