import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import RefreshIcon from '@material-ui/icons/Refresh';
import { adminApi } from 'src/core/api';
import UserListItem from './UserListItem';
import SearchField from './SearchField';
import { calculateAge } from 'src/core/utils';

const useStyles = withStyles(theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },

  listWrapper: {
    overflow: 'auto',
    width: '100%',
    maxWidth: 800,
    maxHeight: 'calc(100vh - 160px)',
  },

  list: {
    width: '100%',
    maxWidth: 800,
    backgroundColor: theme.palette.background.paper,
  },

  toolbar: {
    width: 800,
    alignItems: 'center',
  },

  grow: {
    flexGrow: 999,
  },

}));

class OnlineUserList extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      isFetching: true,
      total: 0,
      femaleUsers: [],
      maleUsers: [],
      otherUsers: [],
      searchUsers: [],
      showFemale: true,
      showMale: false,
      showOther: false,
      showSearch: false,
      showYoung: false,
    };

    this._handleFieldChange = this._handleFieldChange.bind(this);
    this._handleRefresh = this._handleRefresh.bind(this);
    this._handleSearch = this._handleSearch.bind(this);
    this._handleSearchClear = this._handleSearchClear.bind(this);
  }

  componentDidMount() {
    this._handleRefresh();
  }

  render() {
    const { classes } = this.props;
    const { isFetching, total, femaleUsers, maleUsers, otherUsers, searchUsers, showSearch, showFemale, showMale, showOther, showYoung } = this.state;
    let content;
    if (isFetching) {
      content = <CircularProgress />;
    } else if (showSearch) {
      content = (
        <div className={classes.listWrapper}>
          {this._showList('Searching', searchUsers)}
        </div>
      );
    } else {
      content = (
        <div className={classes.listWrapper}>
          {showFemale && this._showList('Female', femaleUsers)}
          {showMale && this._showList('Male', maleUsers)}
          {showOther && this._showList('Others', otherUsers)}
        </div>
      );
    }

    return (
      <div className={classes.container}>
        <FormGroup row className={classes.toolbar}>
          <span style={{ marginRight: 10 }}>Total: {total}</span>
          <FormControlLabel
            control={
              <Checkbox name="showFemale" checked={showFemale} onChange={this._handleFieldChange} value={showFemale} />
            }
            label="Female"
          />
          <FormControlLabel
            control={
              <Checkbox name="showMale" checked={showMale} onChange={this._handleFieldChange} value={showMale} />
            }
            label="Male"
          />
          <FormControlLabel
            control={
              <Checkbox name="showOther" checked={showOther} onChange={this._handleFieldChange} value={showOther} />
            }
            label="Other"
          />
          <FormControlLabel
            control={
              <Checkbox name="showYoung" checked={showYoung} onChange={this._handleFieldChange} value={showYoung} />
            }
            label="Young"
          />
          <IconButton size="small" onClick={this._handleRefresh}>
            <RefreshIcon />
          </IconButton>

          <div className={classes.grow} />

          <SearchField onSearch={this._handleSearch} onClear={this._handleSearchClear} />
        </FormGroup>
        {content}
      </div>
    );
  }

  _showList(title, users) {
    const { classes } = this.props;
    let displayUsers = users;

    if (!this.state.showSearch && !this.state.showYoung) {
      displayUsers = users.filter(user => (user.actualAge > 17));
    }

    return (
      <List className={classes.list} subheader={<ListSubheader>{`${title} (${displayUsers.length})`}</ListSubheader>}>
        {displayUsers.map(user => (
          <UserListItem key={user.id} user={user} />
        ))}
      </List>
    );
  }

  _handleSearch(search) {
    this.setState({
      isFetching: true,
      searchUsers: [],
    });

    adminApi.getUser(search)
      .then(resp => [resp.data])
      .catch(() => [])
      .then((users) => {
        this.setState({
          isFetching: false,
          showSearch: true,
          searchUsers: users,
        });
      });
  }

  _handleSearchClear() {
    this.setState({
      isFetching: false,
      showSearch: false,
      searchUsers: [],
    });
  }

  _handleRefresh() {
    this.setState({
      isFetching: true,
      total: 0,
      maleUsers: [],
      femaleUsers: [],
      otherUsers: [],
    });

    adminApi.listUsers()
      .then((resp) => {
        const maleUsers = [];
        const femaleUsers = [];
        const otherUsers = [];

        resp.data.items.forEach((user) => {
          user.actualAge = calculateAge(user.birthday && new Date(user.birthday)) || 99;

          if (user.gender === 'male') {
            maleUsers.push(user);
          } else if (user.gender === 'female') {
            femaleUsers.push(user);
          } else {
            otherUsers.push(user);
          }
        });

        this.setState({
          isFetching: false,
          total: resp.data.total,
          maleUsers,
          femaleUsers,
          otherUsers,
        });
      });
  }

  _handleFieldChange(event) {
    const { type, name, value, checked } = event.target;

    this.setState({
      [name]: type === 'checkbox' ? checked : value,
    });
  }
}

export default useStyles(OnlineUserList);
