import React from 'react';
import { Route, Redirect } from 'react-router-dom';

export default function PrivateRoute({ component: Component, isAuthenticated, isAllowed, ...rest }) {
  const render = (props) => {
    if (!isAuthenticated) {
      return (
        <Redirect
          to={{
            pathname: '/login',
            state: { from: props.location }
          }}
        />
      );
    }

    if (!isAllowed) {
      return (
        <Redirect
          to={{
            pathname: '/forbidden',
            state: { from: props.location }
          }}
        />
      );
    }

    return (<Component {...props} />);
  };

  return (
    <Route
      {...rest}
      render={render}
    />
  );
}
