export const SHOW = 'NOTIFICATION_SHOW';
export const HIDE = 'NOTIFICATION_HIDE';
export const SET = 'NOTIFICATION_SET';

const queue = [];
let currentMessageKey = 0;

export function show() {
  return {
    type: SHOW,
  };
}

export function hide() {
  return {
    type: HIDE,
  };
}

export function set(message) {
  return {
    type: SET,
    messageText: message,
    messageKey: ++currentMessageKey,
  };
}

export function notify(message) {
  return (dispatch, getState) => {
    const { opened } = getState().notification;

    queue.push(message);

    if (opened) {
      dispatch(hide());
    } else {
      dispatch(processQueue());
    }
  };
}

export function processQueue() {
  return (dispatch) => {
    if (queue.length) {
      dispatch(set(queue.shift()));
      dispatch(show());
    }
  };
}
