import React from 'react';
import { Route } from 'react-router-dom';
import PrivateRoute from 'src/components/PrivateRoute';
import HomeView from './HomeView';
import LoginView from './LoginView';
import ForbiddenView from './ForbiddenView';
import OnlineUsersView from './OnlineUsersView';
import ActiveUsersView from './ActiveUsersView';
import ModerateView from './ModerateView';
import { UserRole } from 'src/constants';
import MappingView from './MappingView';
import BlockUserView from './BlockUserView';

export default function Content() {
  return (
    <React.Fragment>
      <PrivateRoute exact path="/" component={HomeView} />
      <PrivateRoute exact path="/online-users" authzCheck={[UserRole.ADMIN]} component={OnlineUsersView} />
      <PrivateRoute exact path="/active-users" authzCheck={[UserRole.ADMIN]} component={ActiveUsersView} />
      <PrivateRoute exact path="/mappings" authzCheck={[UserRole.ADMIN]} component={MappingView} />
      <PrivateRoute exact path="/block-user" authzCheck={[UserRole.ADMIN]} component={BlockUserView} />
      <PrivateRoute exact path="/moderate" authzCheck={[UserRole.MODERATOR]} component={ModerateView} />
      <PrivateRoute exact path="/forbidden" authzCheck={false} component={ForbiddenView} />
      <Route exact path="/login" component={LoginView} />
    </React.Fragment>
  );
}
