import React from 'react';
import Container from 'src/components/Container';
import useForm from 'src/hooks/useForm';
import { TextField, Button, List, ListSubheader, CircularProgress } from '@material-ui/core';
import { adminApi } from 'src/core/api';
import MappingUserListItem from './MappingUserListItem';
import { makeStyles } from '@material-ui/core/styles';

function getUser(userId, waiter) {
  return adminApi.getUser(userId)
    .then(resp => resp.data)
    .then((user) => {
      user.name = user.name + ` - ${waiter}`;
      return user;
    });
}

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },

  form: {
    width: 700,
    marginBottom: theme.spacing(2),
    display: 'flex',
    justifyContent: 'space-between',
  },

  textField: {
    width: 300,
  },

  list: {
    width: 800,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function MappingView() {
  const classes = useStyles();
  const { values, handleChange } = useForm();
  const [users, setUsers] = React.useState([]);
  const [isFetching, setIsFetching] = React.useState(false);

  React.useEffect(loadData, []);

  function loadData() {
    setIsFetching(true);

    adminApi.listMappings()
      .then((resp) => {
        const mappings = resp.data;
        return Promise.all(Object.keys(mappings).map((target) => {
          return getUser(target, mappings[target]);
        }));
      })
      .then(setUsers)
      .then(() => setIsFetching(false));
  }

  function handleSubmit(event) {
    event.preventDefault();

    setIsFetching(true);
    adminApi.addMapping({ waiter: values.waiter, target: values.target }).then(loadData);
  }

  function handleRemove(user) {
    setIsFetching(true);
    adminApi.removeMapping(user.id).then(loadData);
  }

  return (
    <Container className={classes.container}>
      <form className={classes.form} onSubmit={handleSubmit}>
        <TextField className={classes.textField} name="waiter" placeholder="Waiter" onChange={handleChange} required />
        <TextField className={classes.textField} name="target" placeholder="Target" onChange={handleChange} required />
        <Button type="submit">Add</Button>
      </form>
      {isFetching ? <CircularProgress /> : <div>
        <List className={classes.list} subheader={<ListSubheader>{`Users (${users.length})`}</ListSubheader>}>
          {users.map(user => (
            <MappingUserListItem key={user.id} user={user} onRemove={handleRemove} />
          ))}
        </List>
      </div>}
    </Container>
  );
}
