import { UserRole } from 'src/constants';
import storage from './storage';

export function isAllowed(user) {
  return isModerator(user) || isAdmin(user);
}

export function isAdmin(user) {
  return isRole(UserRole.ADMIN, user.role);
}

export function isModerator(user) {
  return isRole(UserRole.MODERATOR, user.role);
}

export function isRole(role, toCheckRole) {
  return toCheckRole > 0 && (toCheckRole & role) === role;
}

export function getToken() {
  return storage.getItem('token');
}

export function setToken(token) {
  storage.setItem('token', token);
}
