import React from 'react';

const Container = ({ children, className, style, hasSidebar }) => {
  const ownStyle = {};
  const ownClassName = className ? 'Container ' + className : 'Container';

  if (hasSidebar) {
    ownStyle.marginRight = 240;
  }

  return (
    <div style={Object.assign(ownStyle, style)} className={ownClassName}>{children}</div>
  );
};

export default Container;
