import React from 'react';
import Button from '@material-ui/core/Button';
import ConfirmButton from '../ConfirmButton';
import { modApi } from 'src/core/api';

export default function UnblockUserButton({ code, disabled, onUnblockSuccess, onUnblockFailure }) {
  function handleUnblock() {
    modApi.unblockUser(code.toLowerCase())
      .then(onUnblockSuccess)
      .catch(onUnblockFailure);
  }

  return (
    <ConfirmButton
      anchorElement={({onClick}) => (
        <Button variant="outlined" color="primary" onClick={onClick} disabled={disabled}>M kha</Button>
      )}
      dialogTitleText="M kha ti khon"
      dialogBodyText="Bn c chc chn mun m kha ti khon ny?"
      dialogActionText="M kha"
      handleSubmit={handleUnblock}
    />
  );
}
