import { connect } from 'react-redux';
import Notification from './Notification';
import { processQueue, hide } from 'src/actions/notification';

function mapStateToProps(state) {
  const { messageText, messageKey, opened } = state.notification;

  return {
    opened,
    messageText,
    messageKey,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    onExited() {
      dispatch(processQueue());
    },

    onClosed(event, reason) {
      if (reason === 'clickaway') {
        return;
      }

      dispatch(hide());
    }
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Notification);
