import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import IconButton from '@material-ui/core/IconButton';
import RejectIcon from '@material-ui/icons/ThumbDown';
import ApproveIcon from '@material-ui/icons/Done';
import ApprovedIcon from '@material-ui/icons/DoneAll';
import EditIcon from '@material-ui/icons/Edit';
import Tooltip from '@material-ui/core/Tooltip';
import Button from '@material-ui/core/Button';
import InputBase from '@material-ui/core/InputBase';

import { formateDateTime } from 'src/core/utils';
import { Status } from 'src/constants';

import ConfessionCopyContent from '../ConfessionCopyContent';

const useStyles = withStyles(theme => ({
  card: {
    margin: theme.spacing(1),
    width: 600,

    '& .MuiCardHeader-root': {
      paddingTop: theme.spacing(1),
      paddingBottom: theme.spacing(1),
    },

    '& .MuiCardHeader-action': {
      height: 44,
    },
  },
  cardApproved: {
    backgroundColor: '#fbfff8',
  },
  cardRejected: {
    backgroundColor: '#efefef',
  },
  cardContent: {
    paddingTop: 0,
    paddingBottom: 0,
  },
  content: {
    whiteSpace: 'pre-wrap',
  },
  actions: {
    display: 'flex',
    justifyContent: 'flex-end',
    paddingTop: 0,
    paddingBottom: 0,
  },
  numberInput: {
    padding: theme.spacing(1),
    paddingLeft: 0,
    height: 32,
    display: 'flex',
    alignItems: 'center',
  },
  numberDisplay: {
    display: 'flex',
    alignItems: 'center',
    height: 32,
  },
  numberDisplayText: {
    paddingLeft: 0,
    fontSize: '1rem',
  },
  editButton: {
    padding: theme.spacing(1),
  },
  approvedButton: {
    color: 'green',
  },
  metadata: {
    color: 'rgba(0, 0, 0, 0.54)',
    lineHeight: '44px',
  },
}));

class ConfessionListItem extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      showNumberField: false,
      number: 0,
    };

    this._onEditButtonClicked = this._onEditButtonClicked.bind(this);
    this._onSaveButtonClicked = this._onSaveButtonClicked.bind(this);
    this._onNumberInputChanged = this._onNumberInputChanged.bind(this);
  }

  render() {
    const {
      classes,
      confession,
      toggleReject,
      toggleApprove,
    } = this.props;
    const {
      showNumberField,
    } = this.state;
    const rejectColor = confession.status === Status.REJECTED ? 'secondary' : 'default';
    const approvedClassName = confession.status === Status.APPROVED ? classes.approvedButton : '';
    const ApproveComponent = confession.status === Status.APPROVED ? ApprovedIcon : ApproveIcon;
    const date = formateDateTime(new Date(confession.createdAt));
    let className = classes.card;

    if (confession.status === Status.APPROVED) {
      className += ' ' + classes.cardApproved;
    } else if (confession.status === Status.REJECTED) {
      className += ' ' + classes.cardRejected;
    }

    const numberInput = (
      <div className={classes.numberInput}>
        <InputBase
          type="number"
          placeholder="1234..."
          value={this.state.number}
          onChange={this._onNumberInputChanged}
          autoFocus
        />
        <Button size="small" color="primary" onClick={this._onSaveButtonClicked}>Lu</Button>
      </div>
    );
    const numberDisplay = (
      <div className={classes.numberDisplay}>
        <Typography className={classes.numberDisplayText} component="h5">
          {confession.number || '#'}
        </Typography>
        <Tooltip title="Sa s th t">
          <IconButton className={classes.editButton} aria-label="Edit" onClick={this._onEditButtonClicked}>
            <EditIcon fontSize="small" />
          </IconButton>
        </Tooltip>
      </div>
    );

    return (
      <Card className={className}>
        <CardHeader
          title={showNumberField ? numberInput : numberDisplay}
          action={
            <React.Fragment>
              <span className={classes.metadata}>{`${confession.row} | ${date}`}</span>
              {confession.number ? <ConfessionCopyContent confession={confession} /> : null}
            </React.Fragment>
          }
        />
        <CardContent className={classes.cardContent}>
          <Typography className={classes.content} component="pre">
            {confession.content}
          </Typography>
        </CardContent>

        <CardActions className={classes.actions}>
          <Tooltip title="T chi">
            <IconButton onClick={() => toggleReject(confession.id)} color={rejectColor} aria-label="T chi">
              <RejectIcon />
            </IconButton>
          </Tooltip>
          <Tooltip onClick={() => toggleApprove(confession.id)} title="Duyt">
            <IconButton className={approvedClassName} aria-label="Duyt">
              <ApproveComponent />
            </IconButton>
          </Tooltip>
        </CardActions>
      </Card>
    );
  }

  _onEditButtonClicked() {
    this.setState({
      showNumberField: true,
      number: this.props.confession.number || 0,
    });
  }

  _onSaveButtonClicked() {
    this.props.updateNumber(this.props.confession.id, this.state.number);
    this.setState({
      showNumberField: false,
    });
  }

  _onNumberInputChanged(event) {
    this.setState({
      number: parseInt(event.target.value),
    });
  }
}

export default useStyles(ConfessionListItem);
