import { confessionApi } from 'src/core/api';
import { Status, PAGE_SIZE } from 'src/constants';

let lastNumber = 0;

export const FETCH_REQUEST = 'C_FETCH_REQUEST';
export const FETCH_SUCCESS = 'C_FETCH_SUCCESS';
export const SET_STATUS_FILTER = 'C_SET_STATUS_FILTER';
export const TOGGLE_STATUS = 'C_TOGGLE_STATUS';
export const SET_NUMBER = 'C_SET_NUMBER';

export function fetchRequest() {
  return {
    type: FETCH_REQUEST,
  };
}

export function fetchSuccess(items, total, page) {
  return {
    type: FETCH_SUCCESS,
    items,
    total,
    page,
  };
}

export function setStatusFilter(status) {
  return {
    type: SET_STATUS_FILTER,
    status,
  };
}

export function toggleStatus(id, status) {
  return {
    type: TOGGLE_STATUS,
    id,
    status,
  };
}

export function setNumber(id, number) {
  return {
    type: SET_NUMBER,
    id,
    number,
  };
}

export function setNumberWithLast(id, number) {
  return (dispatch) => {
    if (number > lastNumber) {
      lastNumber = number;
    }

    dispatch(setNumber(id, number));
  };
}

export function toggleApprove(id) {
  return (dispatch, getState) => {
    const { items } = getState().confessions;

    const confession = items.filter(item => (item.id === id))[0];

    if (confession) {
      const newStatus = confession.status === Status.APPROVED ? Status.UNCHECKED : Status.APPROVED;
      const number = (!confession.number && newStatus === Status.APPROVED) ? lastNumber + 1 : undefined;

      confessionApi.update(confession.id, { status: newStatus, number })
        .then(() => {
          dispatch(toggleStatus(id, newStatus));

          if (number) {
            dispatch(setNumberWithLast(id, lastNumber + 1));
          }
        });
    }
  };
}

export function toggleReject(id) {
  return (dispatch, getState) => {
    const { items } = getState().confessions;

    const confession = items.filter(item => (item.id === id))[0];

    if (confession) {
      const newStatus = confession.status === Status.REJECTED ? Status.UNCHECKED : Status.REJECTED;
      confessionApi.update(confession.id, { status: newStatus })
        .then(() => {
          dispatch(toggleStatus(id, newStatus));
        });
    }
  };
}

export function loadPage(page) {
  return (dispatch, getState) => {
    dispatch(fetchRequest());

    const { statusFilter } = getState().confessions;
    const query = {
      limit: PAGE_SIZE,
      offset: PAGE_SIZE * page
    };

    if (statusFilter > -1) {
      query.status = statusFilter;
    }

    return confessionApi.list(query).then((resp) => {
      dispatch(fetchSuccess(resp.data.items, resp.data.total, page));
    });
  };
}

export function refresh() {
  return (dispatch, getState) => {
    const { page } = getState().confessions;

    return dispatch(loadPage(page));
  };
}

export function applyStatusFilter(status) {
  return (dispatch) => {
    dispatch(setStatusFilter(status));

    return dispatch(loadPage(0));
  };
}

export function updateNumber(id, number) {
  return (dispatch) => {
    return confessionApi.update(id, { number }).then(() => {
      return dispatch(setNumberWithLast(id, number));
    });
  };
}
