import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import CopyIcon from '@material-ui/icons/FilterNone';
import Tooltip from '@material-ui/core/Tooltip';

export default class ConfessionCopyContent extends React.Component {

  handleCopy = () => {
    this.props.copy(this.props.confession);
  };

  render() {
    return (
      <Tooltip title="Copy  ng Facebook">
        <IconButton style={{ color: 'orange' }} aria-label="Copy" onClick={this.handleCopy}>
          <CopyIcon fontSize="small" />
        </IconButton>
      </Tooltip>
    );
  }
}
