import React from 'react';
import Container from 'src/components/Container';
import useForm from 'src/hooks/useForm';
import { TextField, Button, FormControlLabel, Checkbox, CircularProgress, Typography, Paper } from '@material-ui/core';
import { adminApi } from 'src/core/api';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },

  form: {
    width: 600,
    marginBottom: theme.spacing(2),
    display: 'flex',
    justifyContent: 'space-between',
  },

  textField: {
    width: 300,
  },

  message: {
    width: 600,
    padding: theme.spacing(3),
  },
}));

export default function BlockUserView() {
  const classes = useStyles();
  const { values, handleChange } = useForm({ forceClose: false });
  const [isFetching, setIsFetching] = React.useState(false);
  const [message, setMessage] = React.useState();

  function handleSubmit(event) {
    event.preventDefault();

    setIsFetching(true);
    setMessage('');

    const method = values.forceClose ? 'forceUserCloseChat' : 'blockUser';

    adminApi[method](values.userId)
      .then((resp) => {
        setMessage(JSON.stringify(resp.data));
        setIsFetching(false);
      })
      .catch(() => {
        setMessage('ERROR!');
        setIsFetching(false);
      });
  }

  return (
    <Container className={classes.container}>
      <form className={classes.form} onSubmit={handleSubmit}>
        <TextField className={classes.textField} name="userId" placeholder="User ID" onChange={handleChange} required />
        <FormControlLabel
          control={
            <Checkbox
              name="forceClose"
              checked={values.forceClose}
              onChange={handleChange}
              color="primary"
            />
          }
          label="Force close"
        />
        <Button type="submit">{values.forceClose ? 'Close' : 'Block'}</Button>
      </form>

      <Paper className={classes.message}>{isFetching ? <CircularProgress /> : <Typography variant="caption">{message}</Typography>}</Paper>
    </Container>
  );
}
