import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import CircularProgress from '@material-ui/core/CircularProgress';
import Typography from '@material-ui/core/Typography';
import { modApi } from 'src/core/api';
import SearchField from '../SearchField';
import UnblockUserButton from '../UnblockUserButton';

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },

  content: {
    ...theme.mixins.gutters(),
    marginTop: theme.spacing(2),
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    width: 800,
  },
}));

export default function ModeratePanel() {
  const classes = useStyles();
  const [code, setCode] = React.useState();
  const [user, setUser] = React.useState();
  const [isLoading, setIsLoading] = React.useState(false);

  React.useEffect(() => {
    if (code) {
      setIsLoading(true);
      setUser(null);

      modApi.getUserProfile(code.toLowerCase())
        .then((resp) => {
          setUser(resp.data);
          setIsLoading(false);
        })
        .catch(() => {
          setIsLoading(false);
        });
    } else {
      setUser(null);
    }
  }, [code]);

  let content;

  if (isLoading) {
    content = (
      <CircularProgress />
    );
  } else if (user) {
    content = (
      <div>
        <Typography>Hnh ng:</Typography>
        <UnblockUserButton code={code} disabled={!user.messages.total} />
        <br /><br/>
        <Typography>Tin nhn gn y ({user.messages.total}):</Typography>
        <ul>
          {user.messages.items.map(message => (
            <li>{message.content}</li>
          ))}
        </ul>
      </div>
    );
  } else {
    content = (
      <Typography>Khng tm thy user</Typography>
    );
  }

  return (
    <div className={classes.container}>
      <SearchField placeholder="Nhp code..." onSearch={(code) => setCode(code)} onClear={() => setCode()} />
      <Paper className={classes.content}>{content}</Paper>
    </div>
  );
}
